p{color:#8892b0;font-size:20px;line-height:1.3}.about{min-height:100vh;max-width:900px;margin:auto;counter-reset:numberedHeading}.about .about-inner{display:grid;grid-template-columns:3fr 2fr;gap:50px}.numbered-heading{display:flex;-webkit-box-align:center;align-items:center;position:relative;margin:10px 0px 40px;width:100%;font-size:clamp(22px,5vw,32px);white-space:nowrap;font-weight:600;color:#ccd6f6;line-height:1.1}.numbered-heading::before{counter-increment:numberedHeading;content:"0" counter(numberedHeading) ".";font-family:"SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;color:#9b1f0d;margin-right:10px;font-size:clamp(16px,3vw,20px);line-height:1.1}.numbered-heading::after{content:"";display:block;position:relative;top:0px;width:300px;height:1px;margin-left:20px;background-color:#233554}ul.skills-list{display:grid;grid-template-columns:repeat(2, minmax(140px, 200px));gap:0px 10px;padding:0px;margin:20px 0px 0px;overflow:hidden;list-style:none}ul.skills-list li{position:relative;margin-bottom:10px;padding-left:20px;font-family:"SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;font-size:14px;color:#8892b0}ul.skills-list li::before{content:"▹";position:absolute;left:0px;top:5px;color:#9b1f0d;font-size:14px;line-height:12px}.about-pic{position:relative;max-width:300px}.wrapper{box-shadow:0 10px 30px -15px rgba(2,12,27,.7);transition:var(--transition);display:block;position:relative;width:100%;background-color:#9b1f0d;border-radius:5px}.wrapper:hover,.wrapper:focus{background:transparent}.wrapper:hover img,.wrapper:focus img{filter:none;mix-blend-mode:normal;opacity:1}.wrapper img{border-radius:5px;width:100%;height:100%;opacity:.8;mix-blend-mode:multiply;filter:grayscale(100%) contrast(1)}.wrapper ::before{content:"";width:100%;height:100%;border:2px solid #9b1f0d;position:absolute;top:20px;left:20px;border-radius:5px;z-index:-1}.wrapper :hover+::before{top:15px;left:15px;transition:all .2s linear}@media screen and (max-width: 768px){.about .about-inner{display:block}.about-pic{margin:50px auto 50px;width:70%}}